import React from 'react'
import { Link } from 'react-router-dom'
import { BrowserRouter as Router } from "react-router-dom"

const ItemLista = props => {
    return (
        <div className='card'>
            <img src={props.foto} className="imgtop" />
            <div className='card-body'>
                <h4 className='card-title'>{props.nome} <p>{props.marca}</p></h4>

                <div className='preco'>
                    {Number(props.preco).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}
                </div>

            </div>

            <p></p>
          
            <div className="detalhes">
            
                <button
                    Router to={`/detalhes/${props.id}`} className="btn btn-success btn-block"> Detalhes
                    </button>
            </div>

            <p></p>

        </div>

    )
}

export default ItemLista